@use "base/variables" as v;
@use "base/mixins" as m;

$width_users_1: 2.5vw;
$width_users_2: 12.5vw;
$width_users_3: 12.5vw;
$width_users_4: 12.5vw;
$width_users_5: 10vw;
$width_users_6: 10vw;
$width_users_7: 4vw;
$width_users_8: 6vw;

.users-body {
    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_users_1;
            }

            &:nth-child(2) {
                width: $width_users_2;
            }

            &:nth-child(3) {
                width: $width_users_3;
            }

            &:nth-child(4) {
                width: $width_users_4;
            }

            &:nth-child(5) {
                width: $width_users_5;
            }

            &:nth-child(6) {
                width: $width_users_6;
            }

            &:nth-child(7) {
                width: $width_users_7;
            }

            &:nth-child(8) {
                width: $width_users_8;
            }
        }
    }

    .table-scroll {
        $width_users__extra: 0.75vw;
        max-width: $width_users_1 + $width_users_2 + $width_users_3 + $width_users_4 + $width_users_5 + $width_users_6 + $width_users_7 + $width_users_8 + $width_users__extra;
    }
}