@use "base/variables" as v;
@use "base/mixins" as m;

.table-container {
    overflow-y: auto;
    overflow-x: auto;
    margin-left: 40rem;
    width: 100%;
    max-height: 60vh;

    .panel {
        margin-right: 1rem;
        margin-bottom: 2rem;

        .input-group {
            display: flex;
            align-items: center;
            justify-content: space-between;

            label {
                color: v.$white;
                background-color: v.$pink;
                border: 1px solid v.$pink;
                border-radius: 5rem 0 0 5rem !important;
                padding: 0.5rem;
                padding-left: 1rem;
                font-size: 0.75rem;

                svg {
                    width: 1.25rem;
                    height: 1.25rem;
                }
            }

            #searchBox {
                position: relative;
                left: -0.5rem;
                top: 1px;
                padding: 0.55rem;
                border: 1px solid v.$pink;
                border-radius: 0 5rem 5rem 0;

                &:focus {
                    outline: none;
                    box-shadow: none;
                }
            }
        }
    }

    table {
        table-layout: fixed;
        width: fit-content;
        border-collapse: collapse;

        .header-sticky {
            position: sticky;
            top: 0;
            background-color: v.$white;
            z-index: 1;
        }

        thead {
            background-color: v.$lightPink;
        }

        th {
            vertical-align: middle;

            img {
                width: 1.5rem;
                position: relative;
                top: -0.25rem;
            }
        }

        td {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            vertical-align: middle;
        }

        .disabled-row {
            border: 1px solid lighten($color: v.$blue, $amount: 25%);
            background-color: lighten($color: v.$blue, $amount: 25%);

            &:hover {
                background-color: lighten($color: v.$blue, $amount: 25%) !important;
            }
        }

        select {
            border: 1px solid v.$blue;
            border-radius: 0.5rem;
            background-color: v.$blue;
            color: v.$white;
            padding: 0.5rem 1rem;

            &:focus {
                outline: none;
                box-shadow: none;
            }
        }

        input[type="submit"] {
            background-color: transparent;
            border: none;
            text-decoration: underline;
            color: v.$pink;
        }

        .td-center {
            text-align: center;
        }

        .td-icon {
            
            .buttons-center {
                display: flex;
                justify-content: center;
            }
            width: 10rem;
            margin: auto;

            button {
                &:hover {
                    background-color: inherit !important;
                }

                &:first-of-type {
                    padding-right: 0;
                }

                &:nth-of-type(2) {
                    padding-left: 1rem;
                }

                img {
                    height: 1.75rem;
                    filter: invert(59%) sepia(40%) saturate(326%) hue-rotate(175deg) brightness(95%) contrast(82%);
                }
            }

            i {
                color: v.$blue;
                font-size: 2rem;
            }
        }

        button {
            background-color: transparent;
            border: none;
        }

        .addBtn {
            border: 2px solid v.$black;
            border-radius: 5rem;
            width: 2.5rem;
            height: 2.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}