@use "base/variables" as v;
@use "base/mixins" as m;

.main {
    background-image: url('../../../build/img/bg/welcome.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;

    .links {
        margin: auto 20vw;
        display: flex;
        flex-direction: column;
        margin-top: 70vh;

        @include m.mobile {
            flex-direction: row;
        }

        &>* {
            margin: auto 2rem;
            display: flex;
            flex-direction: column;
            align-items: center;

            a {
                background-color: v.$white;
                color: v.$pink;
                transition: all 0.3s ease;
                &:hover {
                    background-color: lighten($color: v.$pink, $amount: 25%);
                    transition: all 0.3s ease;
                }
            }

            img {
                width: calc(3vw + 10rem);
                margin-bottom: 3rem;
                margin-top: 3rem;

                @include m.mobile {
                    margin-top: calc(5vh + 2rem);
                }
            }
        }
    }
}