@use "base/variables" as v;
@use "base/mixins" as m;

$width_roles_1: 32vw;
$width_roles_2: 7.5vw;
$width_roles_3: 7.5vw;
$width_roles_4: 7.5vw;
$width_roles_5: 7.5vw;
$width_roles_6: 7.5vw;

.roles-body {
    .row-first {
        background-color: darken(v.$gray,5%);
    }
    td {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }
    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_roles_1;
            }

            &:nth-child(2) {
                width: $width_roles_2;
            }

            &:nth-child(3) {
                width: $width_roles_3;
            }

            &:nth-child(4) {
                width: $width_roles_4;
            }

            &:nth-child(5) {
                width: $width_roles_5;
            }

            &:nth-child(6) {
                width: $width_roles_6;
            }
        }
    }

    .table-scroll {
        $width_roles__extra: 0.75vw;
        max-width: $width_roles_1 + $width_roles_2 + $width_roles_3 + $width_roles_4 + $width_roles_5 + $width_roles_6 + $width_roles__extra;
    }

    .thead-roles {
        background-color: v.$white;

        th {
            text-align: center;
        }
    }

    .thead {
        background-color: v.$blue;
        font-weight: normal;
        color: v.$white;
    }
}