@use "base/variables" as v;
@use "base/mixins" as m;

$width_register_1: 5rem;
$width_register_2: 30rem;
$width_register_3: 10rem;
$width_register_4: 12.5rem;
$width_register_5: 10rem;
$width_register_6: 10rem;
$width_register_7: 10rem;
$width_register_8: 10rem;
$width_register_9: 5rem;
$width_register_10: 10rem;
$width_register_11: 5rem;
$width_register_12: 10rem;
$width_register_13: 5rem;
$width_register_14: 10rem;

#toggleIcon {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.icon-animate {
    transform: scale(1.3);
    opacity: 0.7;
}

.ips-register {

    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_register_1;
            }

            &:nth-child(2) {
                width: $width_register_2;
            }

            &:nth-child(3) {
                width: $width_register_3;
            }

            &:nth-child(4) {
                width: $width_register_4;
            }

            &:nth-child(5) {
                width: $width_register_5;
            }

            &:nth-child(6) {
                width: $width_register_6;
            }

            &:nth-child(7) {
                width: $width_register_7;
            }

            &:nth-child(8) {
                width: $width_register_8;
            }

            &:nth-child(9) {
                width: $width_register_9;
            }

            &:nth-child(10) {
                width: $width_register_10;
            }

            &:nth-child(11) {
                width: $width_register_11;
            }

            &:nth-child(12) {
                width: $width_register_12;
            }

            &:nth-child(13) {
                width: $width_register_13;
            }

            &:nth-child(14) {
                width: $width_register_14;
            }
        }
    }

    .table-scroll {
        height: 90vh;
        $width_register__extra: 1.5rem;
        max-width: $width_register_1 + $width_register_2 + $width_register_3 + $width_register_4 + $width_register_5 + $width_register_6 + $width_register_7 + $width_register_8 + $width_register_9 + $width_register_10 + $width_register_11 + $width_register_12 + $width_register_13 + $width_register_14 + $width_register__extra;
    }
}

// Ips view

$width_view_1: 30rem;
$width_view_2: 10rem;
$width_view_3: 15rem;
$width_view_4: 15rem;
$width_view_5: 20rem;
$width_view_6: 20rem;
$width_view_7: 10rem;
$width_view_8: 10rem;
$width_view_9: 10rem;

.ips-view {

    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_view_1;
            }

            &:nth-child(2) {
                width: $width_view_2;
            }

            &:nth-child(3) {
                width: $width_view_3;
            }

            &:nth-child(4) {
                width: $width_view_4;
            }

            &:nth-child(5) {
                width: $width_view_5;
            }

            &:nth-child(6) {
                width: $width_view_6;
            }

            &:nth-child(7) {
                width: $width_view_7;
            }

            &:nth-child(8) {
                width: $width_view_8;
            }

            &:nth-child(9) {
                width: $width_view_9;
            }
        }
    }

    .table-scroll {
        height: 90vh;
        $width_view__extra: 1.5rem;
        max-width: $width_view_1 + $width_view_2 + $width_view_3 + $width_view_4 + $width_view_5 + $width_view_6 + $width_view_7 + $width_view_8 + $width_view_9 + $width_view__extra;
    }

    svg,
    img {
        width: 1.5rem;
        height: 1.5rem;
        transition: transform 0.3s ease;
    }

    svg:hover {
        cursor: pointer;
        transform: scale(1.2);
        transition: transform 0.3s ease;
    }

}